<template>
    <div class="_container">
        <header class="_header">
            <div class="_search">
                <input type="text" class="form-control" placeholder="Search..." v-model="search" debounce="1000"/>
            </div>            
        </header>
        <div class="_body">
            <p><a href="https://tutorials.w3jar.com/tutorial/what-are-html-entities-how-to-write-html-entities/" target="_blank">Learn HTML Entities</a></p>
            <ul class="charList">
                <li v-for="(item,i) in filterEntity" :key="i">
                    <span class="char" v-html="item.character"/>
                    <span class="name" v-text="item.entity_name"/>
                    <span class="num" v-text="item.entity_number"/>
                    <span class="hex" v-text="item.entity_hex"/>
                </li>
            </ul>
            <div class="_noResult" v-if="Object.keys(filterEntity).length === 0 && showNo">
                <h2><span>ðŸ˜ž</span>No Result Found!</h2>
            </div>
        </div>
    </div>
</template>
<script>
export default {
    name:'HtmlEntities',
    data(){
        return{
            search: '',
            allEntities:[],
            showNo:false
        }
    },
    beforeCreate:function(){
        fetch('./html-entities.json')
        .then(response => response.json())
        .then(data => {
            this.allEntities = data;
            this.showNo = true
        })
        .catch((error) => {
            console.error('Error:', error);
        });
    },
    computed:{
        filterEntity: function () {
            var self = this;
            return this.allEntities.filter(function (item) {
                return item.character.toLowerCase().indexOf(self.search.toLowerCase()) >= 0
                || item.entity_name.toLowerCase().indexOf(self.search.toLowerCase()) >= 0
                || item.entity_number.toLowerCase().indexOf(self.search.toLowerCase()) >= 0
                || item.entity_hex.toLowerCase().indexOf(self.search.toLowerCase()) >= 0
                || item.entity_title.toLowerCase().indexOf(self.search.toLowerCase()) >= 0;
            });
        }
    }
}
</script>

<style>
._body{
    padding: 20px;
}
body{
    padding-top: 58px !important;
}
._header{
    background-color: #ffffff;
    position: fixed;
    /* border: 1px solid red; */
    width: 100%;
    top: 0;
    height: 58px;
    border-bottom: 1px solid #dedede;
}
._search{
    display: flex;
    align-items: center;
    height: 100%;
    padding: 0 12px;
    overflow: hidden;
}
._search [type="text"]{
    width: 100%;
    padding: 10px;
    font-size: 16px;
    outline: none;
    border:1px solid #cccccc;
    border-radius: 2px;
}
._search [type="text"]:hover,
._search [type="text"]:focus{
    border-color: #aaaaaa;
}
.charList{
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    margin: 0;
    padding: 0;
    list-style-type: none;
}
.charList li span{
    display: block;
    /* padding: 5px; */
    text-align: center;
    
}
.charList li .char{
    font-weight: bold;
    font-size: 50px;
    /* font-family: 'Roboto Mono', monospace; */
    padding: 5px;
}

.charList li .name,
.charList li .num,
.charList li .hex{
    color: #444444;
    font-family: 'Roboto Mono', monospace;
    margin-bottom: 5px;
}

.charList li{
    /* border: 1px solid red; */
    background-color: #ffffff;
    margin: 10px;
    flex:1 1 auto;
    padding: 10px;
    border: 1px solid #dedede;
    border-radius: 3px;
}
.charList li:hover{
    background-color: #454d66;
    color: #ffffff;
    border-color: rgba(0,0,0,.1);
}
.charList li:hover > .name,
.charList li:hover > .num,
.charList li:hover > .hex{
    color:#ffffff;
}
._noResult h2{
    text-align: center;
}
._noResult h2 span{
    font-size: 80px;
    display: block;
    margin-bottom: 5px;
}
._body p{
    text-align: center;
}
</style>